input[type="checkbox"]{
  vertical-align: middle;
}

label, input{
  display: inline-block;
  vertical-align: middle;
}

.checkbox{
  margin-left: 10px;
  margin-right: 5px;
  text-align: center;
}

.button {
  display: inline-block;
  margin-right: 14px;
  padding-top: 13px;
  padding-bottom: 13px;
  padding-left: 23px;
  padding-right: 23px;
  border-radius: 3px;
  background-color: black;
  box-shadow: 0 2px 1px 0 rgba(0, 0, 0, 0.12);
  transition-property: background-color, box-shadow, color;
  transition-duration: 300ms,300ms,300ms;
  transition-timing-function: ease,ease,ease;
  font-family: Roboto Slab;
  color: white;
  text-align: center;
  text-decoration: none;
}

.hollow {
  background-color: hsla(0,0.0%, 100%%, 0.44);
  box-shadow: 0 2px 1px 0 rgba(0,0,0,0.12), inset 0 0 0 2px black;
  color: black;
  text-shadow: none;
  display: inline-block;
}

.map-marker {
  position: relative;
  display: flex;
  justify-content: center;
  width: 14px;
  height: 14px;
  border-radius: 100%;
  border: 1px solid black;
  background-color: darkorange;
}

.map-marker .map-marker-label {
  display: none;
}

.map-marker:hover {
  cursor: pointer;
  background-color: red;
}

.map-marker:hover .map-marker-label {
  position: absolute;
  display: block;
  white-space: nowrap;
  background-color: white;
  padding: 4px;
  bottom: 16px;
  border-radius: 4px;
  border: 1px solid black;
}

